// Stylesheets
import './scss/main.scss'

window.HeroMenu = (el, configUser) => {

    let config = {
        // Configuraciones por defecto
        breakpoint: 768,          // Delimita el tamaño mínimo de la pantalla para pasar a menu mobil
        subContainer: 'ul',       // Es el contenedor que contiene las opciones del menu, este es el que se oculta en dispositivo mobil
        contentAnchor: 'li',      // Es el contenedor de las anclas, de las opciones del menu
        animation: 'fade',        // La animación por defecto para el menu y para los dropdown
        animationDropdown: false, // Animación única para los Dropdowns
        animationMenu: false,     // Animación única para el menu
        contentButton: false,     // Es el contenido del botón. Se le puede especificar HTML
        contentButtonOpen: false, // Es el contenido que tendrá el botón para abrir el menu
        contentButtonClose: false,// Es el contenido que tendrá el botón para cerrar el menu
        containerButton: false,   // Especifica el contenedor que contiene al botón responsivo
        clickOutOnClose: true,    // Indica si el menu se cerrara si usuario hace click afuera del menu. Si es falso, el usuario se ve obligado a clicker el botón para cerrar

        // Configuraciones del usuario
        ...configUser
    }

    let navbar
    // Intenta seleccionar el elemento pasado por el usuario
    try {
        navbar = document.querySelector(el)
    } catch (error) {
        console.error(`--------------------------\n\n\tImposible selecionar el elemento: |--${el}--|\n\n--------------------------`)
        return false
    }


    /**
     * Colocar las clases de fade o zoom correspondents
     */
    if ( config.animation === 'fade' )
        config.animation = 'hero-menu-animation-fade'

    if ( config.animationDropdown === 'fade' )
        config.animationDropdown = 'hero-menu-animation-fade'

    if ( config.animationMenu === 'fade' )
        config.animationMenu = 'hero-menu-animation-fade'


    /**
     * Agregara las clases oportunas a cada elemento del menu
     */
    try {
        // Clases al contenedor
        if ( !navbar.classList.contains('hero-menu-container') ) 
            navbar.classList.add('hero-menu-container')

        // Sub-contenedor
        navbar.querySelectorAll(`${config.subContainer}`).forEach((subContainer, index) => {

            // Sub-contenedores anidados, se les agrega un dropdown
            if ( index !== 0 ) {
                subContainer.classList.add('hero-menu-dropdown')
                subContainer.parentNode.classList.add(config.animationDropdown ? config.animationDropdown : config.animation, 'hero-menu-dropdown-button')
            }else{
                // Primer sub-contenedor
                subContainer.classList.add('hero-menu-sub-container')
            }

            // Contenedor de las anclas
            subContainer.querySelectorAll(config.contentAnchor).forEach(contentAnchor => {
                contentAnchor.classList.add('hero-menu-content-anchor')
            })
        })

    } catch (error) {
        console.error(`--------------------------\n\n\tHero menu require de:\n\t\t* Contenedor\n\t\t* Sub Contenedor\n\t\t* Content Anchor\n\tSi al menos uno no esta presente, se genera este error \n\n--------------------------`)
        return false    
    }


    /**
     * Soporte para todos los dropdowns
     */
    const dropdowns = document.querySelectorAll('.hero-menu-dropdown-button');
    const callDropdownAction = () => {
        closeAllDropdown()

        dropdowns.forEach(dropdown => {
            if ( window.innerWidth <= config.breakpoint ){
                dropdown.addEventListener('click', e => toggleDropdown(e))
            }else {
                dropdown.addEventListener('mouseenter', e => openDropdown(e.target))
                dropdown.addEventListener('mouseleave', e => closeDropdown(e.target))
            }
        })
    }

    /**
     * Intercambia las clases para abrir u ocultar el dropdown
     *
     * @param e
     */
    const toggleDropdown = e => {
        e.stopPropagation()
        e.target.parentNode.classList.contains('hero-menu-dropdown-open')
            ? closeDropdown(e.target.parentNode)
            : openDropdown(e.target.parentNode)
    }

    /**
     * Abrir o cerrar Dropdown
     * @param e
     */
    const openDropdown  = e => e.classList.add('hero-menu-dropdown-open')
    const closeDropdown = e => e.classList.remove('hero-menu-dropdown-open')


    /**
     * Cierra todos los modales
     */
    const closeAllDropdown = () => {
        dropdowns.forEach(dropdown => {
            dropdown.classList.remove('hero-menu-dropdown-open')
        })
    }

    /**
     * Botón responsivo
     *
     * @type {HTMLButtonElement}
     */
    let btn = document.createElement('button');
    btn.classList.add('hero-menu-button')

    // Clase especial para el botón por defecto
    if (!config.contentButton && (!config.contentButtonOpen || !config.contentButtonClose))
        btn.classList.add('hero-menu-button-default')

    // Configuration especial para el contenido del botón para cerrar y para abrir
    if ( config.contentButtonClose && config.contentButtonOpen ) {
        // Botón personalizado de usuario para abrir
        let divOpen = document.createElement('div');
        divOpen.classList.add('hero-menu-button-open')
        divOpen.innerHTML = config.contentButtonOpen
        btn.appendChild(divOpen)

        // Botón personalizado de usuario para cerrar
        let divClose = document.createElement('div');
        divClose.classList.add('hero-menu-button-close')
        divClose.innerHTML = config.contentButtonClose
        btn.appendChild(divClose)
    }else{
        /**
         * Por defecto el botón tendrá 3 span, o un único HTML que le pase el usuario
         * @type {boolean|string}
         */
        btn.innerHTML = config.contentButton
            ? config.contentButton
            : `<span></span><span></span><span></span>`
    }

    /**
     * Agrega el botón a un elemento personalizado en el DOM pasado por el usuario
     */
    if ( config.containerButton ){
        try {
            btn.classList.add('hero-menu-button-especial-container')
            document.querySelector(config.containerButton).appendChild(btn)
        }catch (error){
            console.error(`--------------------------\n\n\tImposible selecionar el elemento: |--${el}--|\n\n--------------------------`)
            return false
        }
    }else {
        navbar.prepend(btn) // Agregar el botón al contenedor principal
    }



    /**
     * Función encarga del menu responsivo
     */
    const mobileClass = () => {
        if ( window.innerWidth <= config.breakpoint ){
            navbar.classList.add('mobile')
            navbar.classList.remove('open')
            btn.classList.add('hero-menu-button-show')
        }else {
            btn.classList.remove('hero-menu-button-show')
            navbar.classList.remove('mobile', 'open')
        }
        callDropdownAction()
    }



    /**
     * Alterna eventos para el cierre automático
     * y las clases del menu
     *
     */
    const toggleClassNav = () => {
        if ( navbar.classList.contains('open') ){
            navbar.classList.remove('open')
            btn.classList.remove('open')
            document.removeEventListener('click', checkClickUser)
        }else{
            navbar.classList.add('open')
            btn.classList.add('open')
            document.addEventListener('click', checkClickUser)
        }
    }

    /**
     * Event del Botón
     */
    btn.addEventListener('click', toggleClassNav)


    /**
     * Comprueba si el usuario esta haciendo click AFUERA del navbar
     * Comprueba también si esta haciendo click ADENTRO del navbar
     *
     * @param e
     * @returns {boolean}
     */
    const checkClickUser = e => {
        if ( config.containerButton || !config.clickOutOnClose ) return false

        const navbarElements = navbar.querySelectorAll('*')

        for (let i = 0; i < navbarElements.length; i++) {
            if(e.target === navbarElements[i])
                return true

            if ( i+1 === navbarElements.length )
                toggleClassNav()
        }
    }


    // Visualiza si se deben color las clases pertinentes
    window.addEventListener('resize', mobileClass)
    mobileClass() // Invoca la clase para colocar el mobile

    return el
}